.shared-instant-control{
    margin:5% 0;
    padding: 0 10%;
    h1{
        font-size: 35px;
        padding:25px;
        margin-bottom: 30px;
    }
    .items1{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .item1{
        border:1px solid transparent;
        border-radius: 10px;
        width: 22%;
        padding:25px;
    }
    .icon{
        font-size: 45px;
        color:#427cff;
    }
    h6{
        font-size: 20px;
        margin:15px 0;
    }
    p{
        font-size: 18px;
    }
}

@media(max-width:1401px){
    .shared-instant-control{
        h1{
            font-size: 27.5px;
            padding:20px;
            margin-bottom: 20px;
        }
        .item1{
            width: 22.5%;
            padding:20px;
        }
        .icon{
            font-size: 40px;
        }
        h6{
            font-size: 16px;
            margin:15px 0;
        }
        p{
            font-size: 15px;
        }
    }
}


@media(max-width:991px){
    .shared-instant-control{
        h1{
            margin-bottom: 10px;
        }
        .item1{
            width: 45%;
            margin:10px 0;
        }
    }
}


@media(max-width:630px){
    .shared-instant-control{
        padding:0 7.5%;
        h1{
            margin-bottom: 5px;
        }
        .item1{
            width: 47.5%;
            margin:10px 0;
        }
    }
}
@media(max-width:500px){
    .shared-instant-control{
        h1{
            font-size: 20px;
            padding:15px;
        }
        .item1{
            width: 100%;
            margin:5px 0;
            padding: 15px;
        }
    }
}

@-webkit-keyframes hvr-ripple-in {
    100% {
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      opacity: 1;
    }
}
@keyframes hvr-ripple-in {
    100% {
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      opacity: 1;
    }
}


.shared-instant-control{
    .item1{
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-property: transform;
        transition-property: transform;
        &:hover{
            -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
            bordeR:1px solid #eee;
        }
      }
    .item1:before{
        pointer-events: none;
        position: absolute;
        z-index: -1;
        content: '';
        top: 100%;
        left: 5%;
        height: 10px;
        width: 90%;
        opacity: 0;
        background: -webkit-radial-gradient(center, ellipse, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 80%);
        background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0) 80%);
        /* W3C */
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-property: transform, opacity;
        transition-property: transform, opacity;
    }
    .item1:hover:before{
        opacity: 1;
        -webkit-transform: translateY(5px);
        transform: translateY(5px);
    }
}

@media(max-width:500px){
    .shared-instant-control{
        .item1{
            &:hover{
                -webkit-transform: translateY(0);
                transform: translateY(0);
                border:1px solid #fff;
            }
          }
        .item1:before{
            background:none;
            background: none;
        }
        .item1:hover:before{
            opacity: 0;
            -webkit-transform: translateY(0);
            transform: translateY(0);
        }
    }
}