.business-page .sso .leftright-reverse{
    padding: 0 10% 0 0;
    .col1{
        width: 35%;
    }
    p{
        line-height: 1.7;
    }
    .other-elements{
        width: 52.5%;
    }
    .images{
        width: 100%;
        position: relative;
    }
    .bg-img{
        content: url('../../../../assets/Business/bg4.png');
        width: 95%;
        position: absolute;
        bottom:0;
        left: 0;
    }
    .top-img{
        content: url('../../../../assets/Business/img4.png');
        width: 85%;
        position: relative;
        z-index: 2;
        border: 1px solid #f3f3f3;
        border-radius: 10px;
        margin-left: auto;
    }
}

@media(max-width:1401px){
    .business-page .sso .leftright-reverse{
        .col1{
            width: 37.5%;
        }
        .other-elements{
            width: 50%;
        }
    }
}

@media(max-width:767px){
    .business-page .sso .leftright-reverse{
        padding:0 7.5%;
        margin:30px 0;
        .col1{
            width:100%;
            margin:20px 0;
        }
        .other-elements{
            margin-top: 15px;
            width:110%;
            margin-left: -10%;
        }
    }
}


//==================================================
.business-page .sso .row6{
    margin:7.5% 0;
    padding:0 10%;
    h1{
        font-size: 35px;
    }
    .p{
        font-size: 18px;
        margin:15px 0 40px;
    }
    .items6{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .item6{
        border:1px solid #eee;
        width: 30%;
        padding:25px;
        border-radius: 10px;
        &:hover{
            background: linear-gradient(#427CFF,#213E80);
            p,h6, svg{
                color:#fff;
            }
        }
    }
    svg{
        color:#427cff;
        font-size: 45px;
    }
    .item6 p{
        font-size: 18px;
    }
    h6{
        font-size: 19px;
        margin:17.5px 0;
    }
}

@media(max-width:1401px){
    .business-page .sso .row6{
        padding:0 10%;
        h1{
            font-size: 27.5px;
        }
        .p{
            font-size: 15px;
            margin:15px 0 35px;
        }
        .item6{
            width: 30%;
            padding:20px;
        }
        svg{
            font-size: 40px;
        }
        .item6 p{
            font-size: 15px;
        }
        h6{
            font-size: 16px;
            margin:15px 0;
        }
    }
}

@media(max-width:850px){
    .business-page .sso .row6{
        padding:0 7.5%;
       .items6{
           justify-content: center;
       }
        .item6{
            width: 45%;
            margin:10px 2.5%;
        }
    }
}

@media(max-width:550px){
    .business-page .sso .row6{
        margin:55px 0 25px;
        h1{
            font-size: 20px;
        }
        .p{
            margin:10px 0;
        }
        .item6{
            width: 100%;
            margin:10px 0;
        }
    }
}

//==================================
.business-page .sso .business-leftright{
    padding: 0 10%;
    margin:7.5% 0;
    .col1{
        width: 35%;
    }
    p{
        line-height: 1.7;
    }
    .other-elements{
        width: 47.5%;
    }
    .images{
        width: 100%;
        position: relative;
        &:hover{
            .circle{
                background: #dce7ff;
            }
        }
    }
    .top-img{
        content: url('../../../../assets/Business/img5.png');
        width: 85%;
        margin-left: auto;
    }
    .circle{
        transition: 0.3s ease-in-out;
        width: 67.5%;
        padding-top: 67.5%;
        border-radius: 50%;
        background: #f8f8f8;
        position: absolute;
        left:0;
        top:50%;
        transform: translateY(-50%);
    }
    
}

@media(max-width:900px){
    .business-page .sso .business-leftright{
        padding: 0 7.5%;
        .col1{
            width: 40%;
        }        
    }
}

@media(max-width:900px){
    .business-page .sso .business-leftright{
        margin:30px 0;
        .col1{
            width: 100%;
            margin: 20px 0;
        }        
        .other-elements{
            width: 400px;
            max-width: 100%;
            margin:30px auto 5px;
        }
    }
}