.business-page .features .row3{
    margin:7.5% 0;
    padding:0 7.5%;
    display: flex;
    justify-content: space-between;
    .item3{
        width: 31%;
        border:1px solid transparent;
        border-radius: 10px;
        padding:25px;
        display: flex;
        align-items: center;
    }
    .icon{
        width: 75px;
        height: 75px;
        border:1px solid transparent;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        font-size: 45px;
        margin-right: 15px;
        color:#427cff;
        transition: 0.2s ease-in-out;
    }
    .item3 p{
        flex: 1;
        font-size: 18px;
        transition: 0.2s ease-in-out;
    }
}

.business-page .features .row4{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin:5% 0 0;
    padding:0 0 0 10%;
    .info{
        width: 37.5%;
    }
    .img{
        content: url('../../../../assets/Business/img3.png');
        width: 57.5%;
        clip-path: polygon(0 0, 100% 0%, 100% 100%, 20% 100%);
        transition: .35s ease-in-out;
        &:hover{
            clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%);
            transform: scale(0.75);
        }
    }
    h1{
        font-size: 32.5px;
        margin-bottom: 25px;
    }
    .item4{
        display: flex;
        align-items: center;
        margin:20px 0;
        svg{
            font-size: 35px;
            color:#427cff;
            margin-right: 20px;
        }
        p{
            flex: 1;
            font-size: 18px;
        }
    }
}

@media(max-width:1401px){
    .business-page .features .row3{
        padding:0 5%;
        .item3{
            padding:15px 10px;
        }
        .icon{
            width: 65px;
            height: 65px;
            font-size: 40px;
        }
        .item3 p{
            font-size: 15px;
        }
    }
    
    .business-page .features .row4{
        .info{
            width: 40%;
        }
        .img{
            width: 55%;
        }
        h1{
            font-size: 25px;
            margin-bottom: 20px;
        }
        .item4{
            margin:15px 0;
            svg{
                font-size: 30px;
                margin-right: 20px;
            }
            p{
                font-size: 15px;
            }
        }
    }
}

@media(max-width:991px){
    .business-page .features .row3{
        justify-content: center;
        flex-wrap: wrap;
        .item3{
            width: 45%;
            margin:10px 2.5%;
        }
        .icon{
            width: 60px;
            height: 60px;
            font-size: 35px;
        }
    }
}


@media(max-width:645px){
    .business-page .features .row3{
        .item3{
            width: 100%;
            margin:5px 0;
            padding:15px;
        }
        .icon{
            margin-right: 10px;
        }
    }
}

@media(max-width:767px){
    .business-page .features .row4{
        margin:5px 0;
        padding:0 7.5%;
        .info{
            width: 100%;
            margin:20px 0;
        }
        .img{
            width: 120%;
            margin:20px 0 20px -10%;
        }
        h1{
            font-size: 20px;
            margin-bottom: 10px;
        }
    }
}

.business-page .features .row3{
    .item3{
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        -webkit-transition-property: color;
        transition-property: color;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    .item3:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 10px;
        background: linear-gradient(90deg,rgba(66, 124, 255,0.4), rgba(66, 124, 255,1));
        -webkit-transform: scaleX(0);
        transform: scaleX(0);
        -webkit-transform-origin: 0 50%;
        transform-origin: 0 50%;
        -webkit-transition-property: transform;
        transition-property: transform;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
        transition-delay: 0.1s;
        -webkit-transition-delay: 0.1s;

      }
    .item3:hover{
        p, svg{
            color:#fff;
        }
    }
    .item3:hover:before{
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }
}