.business-page{
    overflow: hidden;
}

.business-page .security .row1{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin:5% 0;
    padding:0 12.5%;
    .info{
        width: 37.5%;
    }
    .images{
        width: 40%;
        position: relative;
        &:hover{
            .bg-img{
                transform: scale(1.05);
            }
        }
    }
    h1{
        font-size: 35px;
        line-height: 1.3;
        margin-bottom: 25px;
    }
    p{
        font-size: 18px;
        line-height: 1.7;
    }
    .top-img{
        content: url('../../../../assets/Business/img1.png');
        position: absolute;
        width: 87.5%;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
    }
    .bg-img{
        transition: 0.35s ease-in-out;
        content: url('../../../../assets/Business/bg1.png');
        width: 100%;
    }
}

@media(max-width:1401px){
    .business-page .security .row1{
        margin:5% 0;
        padding:0 10%;
        .info{
            width: 40%;
        }
        .images{
            width: 42%;
        }
        h1{
            font-size: 27.5px;
            line-height: 1.3;
            margin-bottom: 20px;
        }
        p{
            font-size: 15px;
            line-height: 1.7;
        }
        .top-img{
            width: 87.5%;
        }
    }
}

@media(max-width:991px){
    .business-page .security .row1{
        margin:7.5% 0;
        padding:0 7.5%;
        .info{
            width: 47.5%;
        }
        .images{
            width: 45%;
        }
        h1{
            font-size: 22.5px;
        }
    }
}

@media(max-width:767px){
    .business-page .security .row1{
        margin:30px 0;
        .info{
            width: 400px;
            max-width: 100%;
            margin:20px auto;
        }
        .images{
            width: 400px;
            max-width: 90%;
            margin:20px auto;
        }
        p{
            line-height: 1.5;
        }
        h1{
            font-size: 20px;
            margin-bottom: 15px;
        }
    }
}

//=======================================
.business-page .security .left-right{
    margin:5% 0;
    padding:0 10%;
    .other-elements{
        width: 40%;
    }
    .col1{
        width: 40%;
    }
    h1{
        font-size: 35px;
    }
    p{
        line-height: 1.7;
        margin:20px 0 35px;
    }
    .created-img{
        width: 100%;
        position: relative;
        &:hover .bg-img{
            top:35%;
            transform: translateY(-35%);
        }
        &:hover .top-img{
            margin-left: 10%;
        }
    }
    .top-img{
        content: url('../../../../assets/Business/img2.png');
        width: 50%;
        position: relative;
        z-index: 2;
        transition: 0.35s ease-in-out;
    }
    .bg-img{
        content: url('../../../../assets/Business/bg2.png');
        position: absolute;
        width: 60%;
        left: 35%;
        top:50%;
        transform: translateY(-50%);
        transition: 0.35s ease-in-out;
    }
}

@media(max-width:1401px){
    .business-page .security .left-right{
        .other-elements{
            width: 41%;
        }
        .col1{
            width: 45%;
        }
        h1{
            font-size: 27.5px;
        }
        p{
            margin:15px 0 30px;
        }
    }
}

@media(max-width:991px){
    .business-page .security .left-right{
        .other-elements{
            width: 45%;
        }
        .col1{
            width: 47.5%;
        }
        h1{
            font-size: 27.5px;
        }
        p{
            margin:15px 0 30px;
        }
    }
}

@media(max-width:767px){
    .business-page .security .left-right{
        margin:30px 0;
        padding:0 7.5%;
        .other-elements{
            width: 400px;
            max-width: 90%;
            margin:20px auto;
        }
        .col1{
            width: 400px;
            max-width: 100%;
            margin:20px auto;
        }
        h1{
            font-size: 20px;
        }
        p{
            margin:15px 0 25px;
            line-height: 1.5;
        }
    }
}